import React, { Component } from 'react';
import { compose, withProps } from 'recompose';
import Kitchen from '@material-ui/icons/Kitchen';
import { withScriptjs, withGoogleMap, GoogleMap, Marker } from "react-google-maps";
import Geocode from "react-geocode";
import axios from '../axios';

import Fridge from '../fridge.png';

import Map from './Map';
import FridgeModal from './FridgeModal';

class FridgeLocation extends Component {

  state = {
    fridges: [],
    location: {
      address: '252 Broadway Brooklyn NY 11211',
      lat: 40.7,
      lng: -73.9
    },
    fridgeLocation: {}
  }

  getLocation = (fridgeObject) => {
    let streetAddress = fridgeObject.streetAddress;
    let fullAddress = streetAddress.concat(" " , fridgeObject.borough, " NY");
    console.log(streetAddress);
    Geocode.fromAddress(fullAddress).then(
      response => {
        const { lat, lng } = response.results[0].geometry.location;
        this.setState({fridgeLocation: {streetAddress, lat, lng}});
      },
      error => {
        console.error(error);
      }
    );
  }
  componentDidMount() {
    Geocode.setApiKey("AIzaSyDH3YftpQWWA2UGa-JTFjVKHwszKqBHHzk");
    
    axios.get('/fridges.json')
    .then(response => {
      const fetchedFridges = [];
      for (let key in response.data) {
        let f = response.data[key]
        this.getLocation(f);
        fetchedFridges.push({
          ...response.data[key],
          id: key
        });
      }
      console.log(fetchedFridges)
      this.setState({fridges: fetchedFridges})
    })
    .catch(error => {
      console.log(error);
    });
  }


  
  render (){
    
    return (
      <div style={{ height: '90vh', width: '80%', margin: 'auto'}}>
        {this.state.fridges.map(f => (
          <div>
            Name: {f.name}
            Borough: {f.borough}
            Neighborhood: {f.neighborhood}
            Address: {f.streetAddress}
            Contact Group: {f.contactGroup}
          </div>
        ))}
        <Map 
          location={this.state.location}
          zoomLevel={12}
          fridgeLocation={this.state.fridgeLocation}/>
      </div>
    )
  }
}

export default FridgeLocation;