import React from 'react'
import GoogleMapReact from 'google-map-react'
import Kitchen from '@material-ui/icons/Kitchen'

import './map.css'

const Map = ({ location, fridgeLocation, zoomLevel }) => (
  <div className="map">
    <h2 className="map-h2">Find a Fridge Near You</h2>

    <div className="google-map">
      <GoogleMapReact
        distanceToMouse={()=>{}}
        bootstrapURLKeys={{ key: 'AIzaSyDH3YftpQWWA2UGa-JTFjVKHwszKqBHHzk' }}
        defaultCenter={location}
        defaultZoom={zoomLevel}
      >
        {fridgeLocation ? <LocationPin
          lat={fridgeLocation.lat}
          lng={fridgeLocation.lng}
          text={fridgeLocation.streetAddress}
        /> : null}
      </GoogleMapReact>
    </div>
  </div>
);

const LocationPin = ({ text }) => (
  <div className="pin">
    <Kitchen className="pin-icon" />
    <p className="pin-text">{text}</p>
  </div>
);

export default Map;